<template>
  <section-container header-text="Add product" class="add-product-form-container">
    <form @submit.prevent class="add-product-form">
      <div v-show="error" class="form-message form-error-message">{{ error }}</div>

      <div class="input-group">
        <label for="category">Category</label>

        <select @change="onCategoryChange($event.target.value)" id="category" class="input-base category-select">
          <option value="" selected>Chose category</option>
          <option v-for="category in categories" :value="category.id" :key="category.id"> {{ category.name }}</option>
        </select>
      </div>

      <div class="input-group">
        <label for="title">Title <small class="required-field">*</small></label>
        <input v-model="product.title" id="title" type="text" class="input-base"
               placeholder="For example: Iron man suit">
      </div>

      <div class="input-group">
        <label for="location">Location <small class="required-field">*</small></label>
        <input v-model="product.location" id="location" type="text" class="input-base"
               placeholder="For example: Los Angeles, CA">
      </div>

      <div class="input-group">
        <label for="description">Description</label>
        <textarea v-model="product.description" id="description" class="input-base" rows="8"
                  placeholder="For example: Iron man suit"></textarea>
      </div>

      <!-- TODO: add image cropper if image is too large -->
      <div class="input-group">
        <label for="photos">Photos (200x150)</label>
        <image-input @imageListChanged="onImageListChanged" id="photos"></image-input>
      </div>

      <div class="input-group">
        <label for="price">Price</label>
        <input v-model.number="product.price" id="price" type="number" step="0.1" class="input-base"
               placeholder="For example: 29.99">
      </div>

      <div class="btn-center btn-margin-top">
        <button :disabled="$v.$invalid" @click="submit" type="submit" class="btn-primary submit-btn">Continue</button>
      </div>
    </form>
  </section-container>
</template>

<script>
import SectionContainer from '@/components/SectionContainer'
import { mapActions } from 'vuex'
import productCategories from '@/mixins/productCategories'
import { maxLength, maxValue, minLength, minValue, numeric, required } from 'vuelidate/lib/validators'
import ImageInput from '@/components/ImageInput'

export default {
  name: 'AddProduct',
  components: { ImageInput, SectionContainer },
  mixins: [productCategories],
  data () {
    return {
      error: null,
      product: {
        title: '',
        location: '',
        description: '',
        price: 0,
        categoryId: '',
        imageUrls: []
      },
      imageFiles: []
    }
  },
  validations: {
    product: {
      title: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(32)
      },
      location: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(32)
      },
      description: {
        maxLength: maxLength(512)
      },
      price: {
        required,
        numeric,
        minValue: minValue(0.1),
        maxValue: maxValue(9999999)
      },
      categoryId: {
        required
      }
    },
    imageFiles: {
      required
    }
  },
  methods: {
    ...mapActions({
      addProduct: 'addProduct'
    }),
    submit() {
      if (!this.$v.invalid) {
        this.addProduct({ product: this.product, imageFiles: this.imageFiles })
          .then(() => {
            this.$router.push("/")
          })
          .catch((error) => {
            this.error = error.message;
          })
      }
    },
    onCategoryChange(value) {
      this.product.categoryId = value;
      this.$v.product.categoryId.$touch();
    },
    onImageListChanged(imageFiles) {
      this.imageFiles = imageFiles;
      this.$v.imageFiles.$touch();
    }
  }
}
</script>

<style lang="scss" scoped>

.add-product-form-container {
  margin-top: 34px;
  max-width: 1136px;
  box-shadow: 0 2px 42px rgba(0, 0, 0, 0.111233);
  border-radius: 7px;

  .add-product-form {
    margin: 0 auto;
    max-width: 664px;

    .btn-center {
      display: flex;
      justify-content: center;

      .submit-btn {
        max-width: 377px;
      }
    }
  }
}

@include breakpoint-to($xl) {
  .add-product-form-container {
    margin: 20px;
  }
}

@include breakpoint-to($sm) {
  .add-product-form-container {
    margin: 10px;
  }
}
</style>
