<template>
  <ul class="photos-list" id="photos">
    <li v-for="file in imageFiles" class="photo" :key="file.name">
      <span class="file-name">{{ file.originalName }}</span>
      <small @click="removeFile(file.name)" class="remove-photo"></small>
    </li>
    <li class="add-photo">
      <label class="add-photo-label" for="imageInput">
        <input id="imageInput" type="file" accept="image/*" @change="addFile">
      </label>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'ImageInput',
  data() {
    return {
      imageFiles: []
    }
  },
  methods: {
    async addFile(event) {
      const file = event.target.files[0];

      if (file) {
        let reader = new FileReader();

        reader.onload = () => {
          const img = new Image;

          img.onload = () => {
            const width = img.width;
            const height = img.height;

            if (width !== 200 || height !== 150) {
              event.target.value = '';
              alert("Required image size is 202x148px");
              return;
            }

            this.imageFiles.push({
              name: (+new Date()) + '-' + file.name,
              originalName: file.name,
              data: file,
              metadata: { contentType: file.type }
            })
          };
          img.src = reader.result.toString();
        };

        reader.readAsDataURL(file);
      }
    },
    removeFile(name) {
      const index = this.imageFiles.findIndex(file => file.name === name)

      if (index > -1) {
        this.imageFiles.splice(index, 1);
        this.$v.imageFiles.$touch();
      }
    }
  },
  watch: {
    imageFiles(newVal) {
      this.$emit("imageListChanged", newVal);
    }
  }
}
</script>

<style lang="scss" scoped>

.photos-list {
  display: flex;
  flex-flow: row wrap;
  margin-top: 4px;
  padding: 16px 16px 6px 16px;
  background: #F9FAFB;
  border: 1px solid #DEDEE0;

  li {
    width: 92px;
    height: 92px;
    margin-bottom: 10px;
    border-radius: 4px;

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  .photo {
    display: flex;
    align-items: flex-end;
    padding: 6px;
    position: relative;
    background: #E4E4E4 url("../assets/svg/photo.svg") no-repeat center 18px;
    background-size: 32px 32px;

    .file-name {
      display: block;
      overflow-wrap: anywhere;
      overflow: hidden;
      text-overflow: fade;
      text-align: center;
      font-size: 12px;
      line-height: 12px;
      max-height: 36px;
    }

    .remove-photo {
      display: block;
      position: absolute;
      right: 8px;
      top: 8px;
      width: 10px;
      height: 10px;
      background: url("../assets/svg/cross.svg") no-repeat center;
      background-size: 10px 10px;
    }
  }

  .add-photo {
    background: #E4E4E4 url("../assets/svg/plus-icon.svg") no-repeat center;

    .add-photo-label {
      display: block;
      height: 100%;
      width: 100%;
      cursor: pointer;

      input {
        display: none;
      }
    }
  }
}

</style>
